body {
  margin: 0;
}

html {
  --main-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo__image {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__link {
  line-height: 0;
  text-decoration: none;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  justify-content: space-around;
  align-items: center;
  height: 60px;
  padding-bottom: 0;
  text-decoration: none;
  display: flex;
}

.nav__list {
  align-content: center;
  align-items: center;
  margin: 0;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover, .nav__link:active {
  color: var(--main-color);
}
